<template>
  <div id="musicList">
    <div class="musicTop">
      <div>
        <van-icon name="play-circle-o" size="0.4rem"/>
        <span>播放全部</span>
        <span style="fontSize:10px">(共{{songs.length}}首)</span>
      </div>
      <div style="margin-right:0.2rem">
        <van-icon name="bars" size="0.4rem"/>
      </div>
    </div>
    <div class="musicList">
      <van-row v-for="(item,i) in songs" :key="item.id" align="center" style="margin-top:10px;height:1rem">
        <van-col span="2"><span style="margin:12px">{{i+1}}</span></van-col>
        <van-col span="16" class="left" @click="play(i)">
          <h4 style="margin:0px">{{item.name}}</h4>
          <span style="fontSize:6px;color:gray">{{getArtists(item.song.artists)}}</span>
        </van-col>
        <van-col span="4" offset="2" class="right">
          <van-icon v-if="item.song.mvid!=0" name="video-o" size="0.6rem"/>
          <van-icon class="right-img" name="bars" size="0.6rem"/>
        </van-col>
      </van-row>
    </div>
  </div>
</template>

<script>
import {mapMutations} from 'vuex'
export default {
data(){
  return{
    songs:[],
  }
},
methods:{
  ...mapMutations(['updatePlaylist','updatePlaylistIndex']),
    async getSongs(){
        const {data :res} = await this.$http.get('/personalized/newsong?limit=20')
        console.log(res)
        this.songs=res.result
    },
    getArtists(x){
      var a=''
      for(let i in x){
        a+=x[i].name+'/'
      }
      return a
    },
    play(i){
      this.updatePlaylist(this.songs)
      this.updatePlaylistIndex(i)
    }
},
created(){
  this.getSongs()
}
}
</script>

<style lang="less" scoped>
#musicList{
  border-top-left-radius: 0.4rem;
  border-top-right-radius: 0.4rem;
  .musicTop{
  display: flex;
  justify-content: space-between;
  }
  .right{
  .van-icon{
    width: 0.6rem;
  }
  .right-img{
    float: right;

  }
}
}


</style>